import { React, useState } from "react";
import Snackbar from "./Snackbar";

const Navbar = (props) => {
  const [searchData, setSearchData] = useState("");
  const handleSubmit = (e) => {
    e.preventDefault();
    props.url(searchData).then(() => {
      console.log(props.urlData);
    });
    // console.log()
  };

  const handleOnChange = (e) => {
    setSearchData(e.target.value);
  };

  return (
    <>
      <nav className="bg-gradient-to-r from-cyan-400 to-blue-500">
        <div className="max-w-8xl mx-auto px-10 shadow-lg">
          <div className="flex justify-between">
            <div className="flex space-x-7">
              {/* WEBSITE LOGO */}
              <div>
                <a href="/" className="flex items-center py-4 pr-2">
                  <img
                    src="./TwiptureLogo.png"
                    alt="logo"
                    className="h-12 w-12 mr-2"
                  />
                  <span className="font-semibold text-white text-3xl dark:text-gray-900">
                    Twipture
                  </span>
                </a>
              </div>
            </div>
            <div className="">
              <div className="bg-white flex items-center space-x-2 border-2 border-gray-800 rounded-lg h-12 my-4 pr-2 dark:bg-slate-800 dark:border-gray-100">
                <form onSubmit={handleSubmit} className="flex items-center">
                  <input
                    className="md:w-96 pl-4 text-xl focus:outline-none bg-white dark:bg-slate-800 dark:text-slate-50"
                    type="search"
                    name="search"
                    placeholder="Enter  Tweet URL"
                    onChange={handleOnChange}
                    required
                  />
                  <button type="submit" onClick={handleSubmit}>
                    <img src="./search.svg" alt="search-logo" className="w-7" />
                  </button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
