import { React, useState } from "react";
import BottomMenu from "./BottomMenu";
import html2canvas from "html2canvas";
import { Grid } from "react-loader-spinner";

const TweetBackground = (props) => {
  const [verfication, setVerification] = useState(false);
  const [response, setResponse] = useState(false);
  const [watermark, setWatermark] = useState(false);
  const [backgroundColor, setBackgroundColor] = useState(
    "from-purple-500 to-orange-500"
  );

  const [globalHeight, setGlobalHeight] = useState("540px");
  const [globalWidth, setGlobalWidth] = useState("960px");
  const [globalScale, setGlobalScale] = useState("0.95");

  const onVerificationClick = (e) => {
    verfication ? setVerification(false) : setVerification(true);
  };

  const onResponseClick = (e) => {
    response ? setResponse(false) : setResponse(true);
  };

  const onWatermarkClick = (e) => {
    watermark ? setWatermark(false) : setWatermark(true);
  };

  const ondownloadClick = (element) => {
    console.log("download");
  };

  const changeBgSize = (height, width, scale) => {
    setGlobalHeight(height);
    setGlobalWidth(width);
    setGlobalScale(scale);
    console.log(height, width, scale);
    // use query selector and add class of width and height
  };

  function downloadURI(uri, name) {
    var link = document.createElement("a");
    link.download = name;
    link.href = uri;
    link.click();
    //after creating link you should delete dynamic link
    //clearDynamicLink(link);
  }

  //Your modified code.
  function printToFile() {
    var div = document.getElementById("canvas");
    html2canvas(div, {
      onrendered: function (canvas) {
        var myImage = canvas.toDataURL("image/png");
        //create your own dialog with warning before saving file
        //beforeDownloadReadMessage();
        //Then download file
        downloadURI("data:" + myImage, "yourImage.png");
      },
    });
  }

  return (
    <div className="flex flex-col max-w-full min-h-screen justify-center items-center mx-20 ">
      <div
        className={`bg-gradient-to-r ${backgroundColor} flex justify-center items-center relative rounded-xl h-[540px] w-[960px] -mt-15 transition ease-in duration-300`}
        // style="height: 540px;width: 960px;"
      >
        <div className="relative px-4 rounded-lg inline-block">
          <div
            className={`bg-white opacity-[.88] flex flex-col rounded-xl p-4 px-6 dark:bg-slate-800 dark:text-slate-50 scale-[${globalScale}] min-w-[485px]`}
          >
            {props.loading ? (
              <div className="flex flex-row justify-center">
                <Grid color="#00BFFF" height={80} width={80} />
              </div>
            ) : (
              <>
                <div className="flex flex-row items-center">
                  <img
                    src={props.tweetData["profile_image_url"]}
                    alt="profile"
                    className="mr-4 rounded-full w-70px h-70px opacity-[1.2]"
                  />
                  <div className="flex-column">
                    <p className="font-medium text-2xl tracking-wide flex flex-row items-center">
                      {props.tweetData["name"]}{" "}
                      <img
                        src="https://upload.wikimedia.org/wikipedia/commons/e/e4/Twitter_Verified_Badge.svg"
                        alt="verification badge"
                        className={
                          verfication
                            ? "inline verbadge ml-1 w-6 hidden"
                            : "inline verbadge ml-1 w-6"
                        }
                      ></img>
                    </p>
                    <p className="text-slate-500 text-base tracking-wide dark:text-slate-400">
                      @{props.tweetData["username"]}
                    </p>
                  </div>
                  <img
                    src="./twitter.svg"
                    alt="twitter-logo"
                    className="h-10 absolute right-0 mr-8 -mt-2"
                  />
                </div>

                <div className="p-2 px-4 mx-12">
                  <p className="flex-col justify-center text-base opacity-90 whitespace-pre-wrap max-h-[325px] max-w-[450px] overflow-hidden">
                    {props.tweetData["text"]}
                  </p>
                </div>
                <div
                  className={
                    response
                      ? " p-2 px-4 ml-12 mt-2 flex flex-row hidden"
                      : " p-2 px-4 ml-12 mt-2 flex flex-row"
                  }
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    aria-hidden="true"
                    focusable="false"
                    className="mr-2 dark:fill-white"
                  >
                    <path opacity="0" d="M0 0h24v24H0z"></path>
                    <path d="M14.046 2.242l-4.148-.01h-.002c-4.374 0-7.8 3.427-7.8 7.802 0 4.098 3.186 7.206 7.465 7.37v3.828c0 .108.044.286.12.403.142.225.384.347.632.347.138 0 .277-.038.402-.118.264-.168 6.473-4.14 8.088-5.506 1.902-1.61 3.04-3.97 3.043-6.312v-.004-.005-.008c-.006-4.367-3.43-7.787-7.8-7.788zm3.787 12.972c-1.134.96-4.862 3.405-6.772 4.643V16.67c0-.414-.335-.75-.75-.75h-.396c-3.66 0-6.318-2.476-6.318-5.886 0-3.534 2.768-6.302 6.3-6.302l4.147.01h.002c3.532 0 6.3 2.766 6.302 6.296-.003 1.91-.942 3.844-2.514 5.176z"></path>
                  </svg>
                  {props.tweetData["reply_count"]}
                  <svg
                    viewBox="0 0 24 24"
                    width="24"
                    height="24"
                    aria-hidden="true"
                    className="ml-16 mr-2 dark:fill-white"
                  >
                    <path d="M23.77 15.67c-.292-.293-.767-.293-1.06 0l-2.22 2.22V7.65c0-2.068-1.683-3.75-3.75-3.75h-5.85c-.414 0-.75.336-.75.75s.336.75.75.75h5.85c1.24 0 2.25 1.01 2.25 2.25v10.24l-2.22-2.22c-.293-.293-.768-.293-1.06 0s-.294.768 0 1.06l3.5 3.5c.145.147.337.22.53.22s.383-.072.53-.22l3.5-3.5c.294-.292.294-.767 0-1.06zm-10.66 3.28H7.26c-1.24 0-2.25-1.01-2.25-2.25V6.46l2.22 2.22c.148.147.34.22.532.22s.384-.073.53-.22c.293-.293.293-.768 0-1.06l-3.5-3.5c-.293-.294-.768-.294-1.06 0l-3.5 3.5c-.294.292-.294.767 0 1.06s.767.293 1.06 0l2.22-2.22V16.7c0 2.068 1.683 3.75 3.75 3.75h5.85c.414 0 .75-.336.75-.75s-.337-.75-.75-.75z"></path>
                  </svg>
                  {props.tweetData["retweet_count"]}
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="24"
                    height="24"
                    viewBox="0 0 24 24"
                    aria-hidden="true"
                    focusable="false"
                    className="ml-16 mr-2 dark:fill-white"
                  >
                    <path opacity="0" d="M0 0h24v24H0z"></path>
                    <path d="M12 21.638h-.014C9.403 21.59 1.95 14.856 1.95 8.478c0-3.064 2.525-5.754 5.403-5.754 2.29 0 3.83 1.58 4.646 2.73.814-1.148 2.354-2.73 4.645-2.73 2.88 0 5.404 2.69 5.404 5.755 0 6.376-7.454 13.11-10.037 13.157H12zM7.354 4.225c-2.08 0-3.903 1.988-3.903 4.255 0 5.74 7.034 11.596 8.55 11.658 1.518-.062 8.55-5.917 8.55-11.658 0-2.267-1.823-4.255-3.903-4.255-2.528 0-3.94 2.936-3.952 2.965-.23.562-1.156.562-1.387 0-.014-.03-1.425-2.965-3.954-2.965z"></path>
                  </svg>
                  {props.tweetData["like_count"]}
                </div>
                <p
                  className={
                    watermark
                      ? "  hidden "
                      : "  absolute w-full text-white text-center items-center dark:text-slate-800 z-110 text-sm -bottom-[1.5rem] pr-[3.75rem]"
                  }
                >
                  Made with  Twipture
                </p>
              </>
            )}
          </div>
        </div>
      </div>
      <BottomMenu
        downloadImage={printToFile}
        verificationClick={onVerificationClick}
        responseClick={onResponseClick}
        watermarkClick={onWatermarkClick}
        downloadTweet={ondownloadClick}
        darkToggleSwitch={props.darkToggleSwitch}
        changeBgSize={changeBgSize}
        changeBgColor={setBackgroundColor}
      />
    </div>
  );
};

export default TweetBackground;
