import { React } from "react";

const BgColorList = [
  {
    name: "Color 1",
    color: "from-cyan-500 to-blue-500",
  },
  {
    name: "Color 2",
    color: "from-yellow-500 to-red-500",
  },
  {
    name: "Color 3",
    color: "from-red-500 to-blue-400",
  },
  {
    name: "Color 4",
    color: "from-purple-500 to-blue-500",
  },
  {
    name: "Color 5",
    color: "from-pink-500 to-red-500",
  },
  {
    name: "Color 6",
    color: "from-pink-500 to-orange-500",
  },
  {
    name: "Color 7",
    color: "from-yellow-500 to-purple-500",
  },
  {
    name: "Color 8",
    color: "from-red-500 to-purple-500",
  },
  {
    name: "Color 9",
    color: "from-green-500 to-cyan-500",
  },
  {
    name: "Color 10",
    color: "from-cyan-500 to-pink-500",
  },
  {
    name: "Color 11",
    color: "from-green-500 to-blue-500",
  },
];

const BackgroundModal = (props) => {
  const BgColors = BgColorList.map((bgcolor, i) => {
    const colorValue = bgcolor.color;

    return (
      <div
        key={i}
        onClick={() => {
          props.changeBgColor(colorValue);
          props.BackgroundModal(false);
        }}
        className={`bg-gradient-to-r mx-2 my-1 cursor-pointer hover:scale-110 w-20 h-20 rounded ${colorValue}`}
      ></div>
    );
  });
  return (
    <>
      <div className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none">
        <div className="relative w-auto my-6 mx-auto max-w-3xl">
          {/*content*/}
          <div className="border-0 rounded-xl shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none dark:bg-slate-700">
            {/*header*/}
            <div className="flex items-start justify-between p-6 rounded-xl rounded-b-none dark:bg-slate-800 bg-slate-200 dark:text-slate-50">
              <h3 className="text-lg font-semibold">Choose Background</h3>
              <div
                onClick={() => props.BackgroundModal(false)}
                className="rounded-full  ml-auto float-right cursor-pointer"
              >
                <img src="./red-cross.png" className="h-6 mx-2" />
              </div>
            </div>
            {/*body*/}
            <div className="relative p-6 px-8 grid grid-cols-5 gap-1 justify-center items-center dark:text-gray-300 text-sm overscroll-auto h-[18rem] overflow-y-scroll">
              {BgColors}
            </div>
            {/*footer*/}
            <div className="flex items-center justify-end p-3 border-t border-solid border-blueGray-200 rounded-b">
              <button
                className="text-red-500 background-transparent font-bold uppercase px-6 py-2 text-sm outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150 hover:bg-red-500 rounded hover:text-white"
                type="button"
                onClick={() => props.BackgroundModal(false)}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
    </>
  );
};

export default BackgroundModal;
