import { React } from "react";

const socailsList = [
  {
    src: "./SquareResize.png",
    name: "Square",
    height: "550px",
    width: "550px",
    scale: "0.9",
  },
  {
    src: "./Landscape.png",
    name: "Landscape",
    height: "540px",
    width: "960px",
    scale: "0.95",
  },
  {
    src: "./Portrait.png",
    name: "Portrait",
    height: "640px",
    width: "360px",
    scale: "0.68",
  },
];

const ResizeModal = (props) => {
  const socialItems = socailsList.map((social, i) => {
    const height = social.height;
    const width = social.width;
    const scale = social.scale;
    return (
      <div
        key={i}
        onClick={() => {
          props.changeBgSize(height, width, scale);
          props.ResizeModal(false);
        }}
        className="flex flex-col justify-center items-center mx-4 my-2 cursor-pointer hover:scale-110"
      >
        <img src={social.src} className="h-12 mb-1" alt="social" />
        <p>{social.name}</p>
      </div>
    );
  });
  return (
    <>
      <div className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none">
        <div className="relative w-auto my-6 mx-auto max-w-3xl">
          {/*content*/}
          <div className="border-0 rounded-xl shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none dark:bg-slate-700">
            {/*header*/}
            <div className="flex items-start justify-between p-5 rounded-xl rounded-b-none dark:bg-slate-800 bg-slate-200 dark:text-slate-50">
              <h3 className="text-lg font-semibold">Resize Image</h3>
              <div
                onClick={() => props.ResizeModal(false)}
                className="rounded-full  ml-auto float-right cursor-pointer"
              >
                <img src="./red-cross.png" alt="close" className="h-6" />
              </div>
            </div>
            {/*body*/}
            <div className="relative p-6 px-20 grid grid-cols-3 gap-3 justify-center items-center dark:text-gray-300 text-sm">
              {socialItems}
            </div>

            {/*footer*/}
            <div className="flex items-center justify-end p-3 border-t border-solid border-blueGray-200 rounded-b">
              <button
                className="text-red-500 background-transparent font-bold uppercase px-6 py-2 text-sm outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150 hover:bg-red-500 rounded hover:text-white"
                type="button"
                onClick={() => props.ResizeModal(false)}
              >
                Close
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
    </>
  );
};

export default ResizeModal;
