import { useState, useEffect } from "react";
import "./App.css";
import Snackbar from "./components/Snackbar";
import GetTweet from "./GetTweet";
import Navbar from "./components/Navbar";
import TweetBackground from "./components/TweetBackground";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";

const App = () => {
  const [dark, setDark] = useState(false);
  const [loading, setLoading] = useState(false);
  const [url, setUrl] = useState("");
  const [isShowingSnackbar, setShowingSnackbar] = useState(false);
  const [message, setMessage] = useState("");

  if (isShowingSnackbar === true) {
    setTimeout(function () {
      setShowingSnackbar(false);
    }, 3000);
  }

  const [tweetData, setTweetData] = useState({
    username: "BhaleraoAkhil",
    profile_image_url:
      "https://pbs.twimg.com/profile_images/1422368757909843969/rh7mE0RD_normal.jpg",
    name: "Akhil Bhalerao",
    text: "Welcome to Twipture , a free service to convert your beautiful tweets to even more beautiful images!",
    created_at: "2022-02-02T10:37:30.000Z",
    retweet_count: "157.4K",
    reply_count: "10.3K",
    like_count: "92.4K",
    quote_count: "479",
  });

  const setTweetUrl = async (value) => {
    await setUrl(value);
    setLoading(true);
    getTwitterData(value);
  };

  const getTwitterData = async (url) => {
    await fetch("https://twipture.herokuapp.com/api/get-tweet-data", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        pass: "kc9AxuolkGgVL5Le",
        tweet_url: url,
      }),
    })
      .then((response) => {
        if (response.ok) {
          response.json().then((data) => {
            if (data["success"] === true) {
              setTweetData(data["message"]);
              setLoading(false);
            }
          });
        } else if (response.status === 400) {
          setMessage("Invalid Url ");
          setShowingSnackbar(true);
        } else {
          setMessage("Something went wrong ");
          setShowingSnackbar(true);
        }
      })
      .catch((error) => {
        setMessage("Something went wrong ");
        setShowingSnackbar(true);
      });
    setLoading(false);
  };

  const toggleDark = () => {
    console.log("dark toggle", dark);
    dark ? setDark(false) : setDark(true);
  };

  return (
    <div className={dark ? "App" : "App dark"}>
      <div className="dark:bg-slate-800">
        <Navbar
          loader={setLoading}
          url={setTweetUrl}
          urlData={url}
          gettweetdata={getTwitterData}
        />
        <TweetBackground
          darkToggleSwitch={toggleDark}
          tweetData={tweetData}
          loading={loading}
        />

        {/* Snackbar */}
        <div
          id="snackbar"
          className={`absolute left-1/2 -translate-x-2/4 ease-in-out duration-500 top-4 bg-orange-500 w-max p-2 px-5 rounded-lg mx-6 dark:text-white drop-shadow-xl font-semibold text-slate-800 z-20 ${
            isShowingSnackbar ? "visible" : "hidden"
          }`}
        >
          <p className="flex flex-column items-center">
            <img
              src="./warning.png"
              alt="warning"
              className="w-7 mr-2 -ml-1"
            ></img>
            {message}
          </p>
        </div>
        {/* End Snackbar */}

        <p className="text-center text-slate-500 mt-2 text-sm dark:text-slate-300">
          Inspired by{" "}
          <a className="font-semibold" href="https://capturemytweet.in">
            <u>Capture my Tweet</u>
          </a>
        </p>
        <p className="text-center text-slate-700 text-sm dark:text-slate-100 pt-2 pb-3">
          &#60;/&#62; with {" "}
          <a
            href="https://www.linkedin.com/in/akhilbhalerao/"
            className="font-semibold"
          >
            <u>Akhil Bhalerao</u>
          </a>
        </p>
      </div>
    </div>
  );
};

export default App;
